  <v-container id="inventory" text-xs-center>
    <div>
      <h2>서울시</h2>
      <v-btn color="secondary" 
        dark 
        v-for="(adds, index) in address"
        :key="index"
        @click="getStoreBy(adds.add)">
        {{ adds.tit }}
      </v-btn>
    </div>
    <v-list subheader>
      <v-subheader>100개 이상(<span class="green">녹색</span>) / 30개 이상 100개미만(<span class="yellow">노랑색</span>) / 2개 이상 30개 미만(<span class="red">빨강색</span>) / 1개 이하(<span class="grey">회색</span>) / 집계 안됨(<span>흰색</span>)</v-subheader>
        <v-list-item
          v-for="(store, index) in stores"
          :key="index"
        >
          <v-list-item-content>
            <v-list-item-title>{{ store.name }} <span class="check" :class="getColor(store.remain_stat)"></span></v-list-item-title>
            <v-list-item-subtitle v-text="store.addr"></v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-icon @click="getGoogleMap(store.lat, store.lng)">
            <i class="material-icons">
              add_location
            </i>
          </v-list-item-icon>
        </v-list-item>
    </v-list>
  </v-container>
</template>

