import { API_URL } from '^@/API';
import { globalEvent } from '^@/event';
export default {
  name: 'Sales',
  data: () => ({
    paging: {
      page:1,
      length: 5,
      totalCount: null
    },
    address: [
      {
        add: '서울특별시 강남구',
